<template>
  <v-app-bar flat app>
    <v-container>
      <v-row align="center">
        <v-img
          :src="require('@/assets/logo.png')"
          class="mr-5"
          height="36"
          width="36"
          max-width="36"
          @click="$vuetify.goTo(0)"
        />

        <v-col v-for="(link, i) in links" :key="i" cols="3" md="auto" class="px-md-3 px-0">
          <v-btn text :to="link.to" :small="$vuetify.breakpoint.smAndDown">{{link.text}}</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-app-bar>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      links: [
        {
          to: "/gesti",
          text: "I Gesti"
        },
        {
          to: "/volto",
          text: "Il linguaggio del volto"
        }
      ]
    };
  },
  methods: {
    //   ...mapMutations(['toggleDrawer']),
    onClick(e, item) {
      e.stopPropagation();
      if (item.to || !item.href) return;
      this.$vuetify.goTo(item.href);
    }
  }
};
</script>

<style scoped>
.v-image {
  border-radius: 2em;
}

.v-btn {
  letter-spacing: 0.06em;
}
</style>