import Vue from 'vue'
import VueRouter from 'vue-router'
import FacialExpressions from '../views/FacialExpressions.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: () => "/gesti"
  }, 
  {
    path: '/volto',
    name: "FacialExpressions",
    component: FacialExpressions
  },
  {
    path: '/gesti',
    name: "Gestures",
    component: () => import( /* webpackChunkName: "about" */ '../views/Gestures.vue')
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return {
      x: 0,
      y: 0
    }
  }
})

export default router